/**
 * 
 * 
 * @export
 * @param {array} 
 * @param {int} x  
 * @param {int} y 
 * @param {string} dir  //up down left right
 * @return {any}  // false  0 1 
 */
export function detectCollision(status, x, y, dir) {
    x = +x;
    y = +y;
    // 
    const dir2num = {
        'up': 0,
        'down': 2,
        'right': 1,
        'left': 3
    };
    // 
    let nextBlock = {
        type: -1, // 
        x: null, // 
        y: null // 
    };
    switch (dir2num[dir]) {
        case 0:
            nextBlock.type = status[x - 1][y];
            nextBlock.x = x - 1;
            nextBlock.y = y;
            break;
        case 1:
            nextBlock.type = status[x][y + 1];
            nextBlock.x = x;
            nextBlock.y = y + 1;
            break;
        case 2:
            nextBlock.type = status[x + 1][y];
            nextBlock.x = x + 1;
            nextBlock.y = y;
            break;
        case 3:
            nextBlock.type = status[x][y - 1];
            nextBlock.x = x;
            nextBlock.y = y - 1;
            break;
        default:
            nextBlock.type = -1;
            break;
    }
    return nextBlock.type === 2 ? false : nextBlock;
}


