import {
    detectCollision
} from './detectCollision';
import {
    move
} from './move.js';
/**
 * 
 * @export
 */
export function update(ctx, dir) {
    const people = document.querySelector('.people'); // 
    let savedPeople = ctx.curStatus.people; // 
    let detectCollisionWith = detectCollision.bind(ctx, ctx.curStatus.map); // 
    let firstDetect = detectCollisionWith(savedPeople.x, savedPeople.y, dir); // 
    console.log(firstDetect);
    if (!firstDetect ) {
        move(people, ctx.option.base, dir);
    } else if (firstDetect.type === 3) {
        let secondDetect = detectCollisionWith(firstDetect.x, firstDetect.y, dir);
        let box = ctx.curStatus.map[secondDetect.x][secondDetect.y].object;
        if (!secondDetect.type) {
            move(box,ctx.base,dir); // 
            move(people, ctx.base, dir); // 
        } else {
            // do nothing
        }
    }
}


