/**
 * 
 * @export
 * @param {string} Selector css 
 * @param {string} eventName 
 * @param {function} func 
 */
export function bindEvent(Selector, eventName, func) {
    let element = document.querySelector(Selector);
    element['on' + eventName] = func;
}


